<template>
    <div class="pro-list">
        <div class="pro-index"
            v-for="v in proList" :key="v.id"
        >
            <img :src="v.url" alt="">
            <p>
                <i>{{v.name}}</i>
                <span>已售：{{v.num}}</span>
            </p>
            <div>
                <strong>￥{{v.prace}}</strong>
                <span>￥{{v.oldPrace}}</span>
            </div>
        </div>
        
    </div>
</template>
<script>
import Vue from 'vue';
import axios from 'axios';
export default {
    name:'pro-list',
    data () {
        return {
            proList:[]
        }
    },
    methods: {
        get(){
            axios.get('./static/proList.json')
            .then(res=>{
                this.proList = res.data;
            }).catch(err=>{
                console.log(err)
            })
        }
    },
    created () {
        this.get();
    }
}
</script>

<style lang="scss" scoped>
    .pro-list{
        overflow: hidden;
        padding: 0.05rem;
    }
    .pro-list .pro-index{
        float: left;
        border: #ddd 0.01rem solid;
        padding: 0.05rem;
        margin: 0.05rem;
        >p{
            height: 0.4rem;
            line-height: 0.4rem;
            border-top: #ddd 0.02rem solid;
            border-bottom: #ddd 0.02rem dotted;
            i{
                color: #333;
            }
            span{
                font-size: 0.12rem;
                color: #999;
                margin-left: 0.1rem;
            }
        }
        >div{
            height: 0.3rem;
            line-height: 0.3rem;
            color: #f60;
            span{
                color: #999;
                text-decoration: line-through;
                margin-left: 0.1rem;
            }
        }
    }
    .pro-list .pro-index img{
        width: 1.8rem;
        height: 1.8rem;
    }
</style>



