import { FLAGINIT } from './const'
export default{
    
    checkMsg({commit},ismsg){
        setTimeout(()=>{
            let msg = getMsg();
            let isHas = msg.some(item=>{
                if(ismsg.username === item.username&&ismsg.password){
                    alert('登录成功');
                    ismsg.$router.push('/index')
                    return true;
                }
            })
            if(!isHas){
                commit(FLAGINIT,true)
                alert('登录失败')
            }
        },200)
    },
    addMsg({commit},ismsg){
        setTimeout(()=>{
            let msg = getMsg();
            let isHas = msg.some(item=>{
                if(ismsg.username === item.username){
                    alert('用户名已存在！请重新输入');
                    return true;
                }
            })
            if(!isHas){
                msg.push({username:ismsg.username,password:ismsg.password});
                alert('注册成功');
                ismsg.$router.push('/index')
            }
            localStorage.msg = JSON.stringify(msg)
        },200)
    }
}
function getMsg(){
    return JSON.parse(localStorage.msg?localStorage.msg:'[]')
}


