<template>
    <div class="maincont">
        <div class="header-top">
            <router-link to="/index" class="back-off f1">
                <span class="fa fa-chevron-left"></span>
            </router-link>
            <div class="head-mid">
                <h1>购物车</h1>
            </div>
        </div>
        <div class="head-top">
            <img src="../../static/images/head.jpg" alt=""
            >
        </div>
        <div class="conent">
            <div class="c-t">
                <span>
                 购物车共有：<b>{{cars.length}}</b>件商品
                </span>
                <span class="fa fa-shopping-cart fa-2x "></span>
            </div>
            <div id="check">
                <input type="checkbox" >全选
            </div>
                  
            <ul>
                <li
                    v-for="v in cars" :key="v.id"
                >
                   <input type="checkbox" name="" id="che">
                  <img :src="v.url" />  
                   <div>
                       <h3>{{v.name}}</h3>
                       <p>单价：{{v.prace}}</p>
                       <p>数量：{{v.numb}}</p>
                   </div>
                   <div id="num">
                       <input type="button" value="-"
                        @click="reduceCar(v)"
                       >
                       <input type="button" value="+"
                       @click="addCar(v)"
                       >
                   </div>
                </li>
            </ul>
        </div>
        <div class="allprace">
            <span>总价：<i>{{allP.prace}}</i></span>
            <button>结算</button>
        </div>
    </div>
</template>
<script>
import {mapState,mapActions,mapGetters} from 'vuex'
import Last from './last/Last'
export default {
    name:"Shoppingcar",
    computed:{
        ...mapGetters(['allP']),
        ...mapState({
          cars: state => state.buyCar.cars  
        })
    },
    methods:{
        ...mapActions(['addCar','reduceCar'])
    },
    created(){
    }
}
</script>
<style lang="scss" scoped>
*{
    margin: 0;
    padding: 0;
}
.maincont {
    width: 100%;
    margin: 0 auto;

}
.header-top {

    padding: 0 0.4rem;
    height: 0.4rem;
    position: relative;
    background: #f1f1f1;

}

.back-off {

    position: absolute;
    left: 0;
    top: 0;
    width: 0.4rem;
    height: 0.4rem;
    text-align: center;
    line-height: 0.4rem;
    font-size: 1.4em;

}
.fl {

    float: left;

}
.head-mid h1 {

    font-size: 0.2rem;
    text-align: center;
    height: 0.4rem;
    line-height: 0.4rem;

}
.head-top {

    position: relative;
    height: auto;

}
.head-top img{
    width: 100%;
    height: auto;
    }
.conent{
    width:100%;
    padding: 0.05rem;
}
.conent .c-t{
    height: 0.4rem;
 display: flex;
   justify-content: space-between;
   align-items: center;
   border-top:solid 0.01rem #666;
   border-bottom: solid 0.01rem #666;
padding-right:1rem;
margin-bottom: .05rem;
}
.conent .c-t span:first-child{
    width:2.5rem;
    height:0.25rem;
    border-right: solid 0.02rem #ccc;
    color:rgba(255, 94, 0, 0.596);
    flex-wrap: wrap;
}
.conent .c-t span{
    color:rgba(255, 94, 0, 0.596);
}
.conent .c-t ul{
    width: 100%;
    
}
.conent #check{
    line-height: .25rem;
    width: 100%;
    height: .25rem;
    border-top:solid 0.01rem rgba(51, 51, 51, 0.63);
    border-bottom: solid 0.01rem rgba(51, 51, 51, 0.63);
    margin-bottom: .1rem;
}
.conent ul li{
    line-height: .35rem;
    width: 99%;
    height: 1rem;
    border-bottom: solid 0.01rem #555;
    display: flex;
    justify-content: space-start;
    align-items: center;
    }
.conent ul li input:first-child{
    width:.3rem;

}
.conent ul li img{
    width: .52rem;
    height: .52rem;
}
.conent ul li div{
    width:2rem;
    height: 1rem;
}
.conent ul li div h3{
    width: 2rem;
    height: .5rem;
    font:700 .14rem/.5rem "";color:rgb(87, 41, 41);
}
.conent ul li div p{
    width: 2rem;
    height: .2rem;
    font-size:.14rem;
    color:rgb(247, 65, 65)；
}
.conent ul li #num{
    display: flex;
    justify-content: space-around;
    width: 1rem;
    height: .3rem;
}
.conent ul li #num input{
   width: .3rem;
   background:rgba(253, 42, 5, 0.596);
   color:#fff;
   border-radius: .05rem;
   border:0;
}
.allprace{
    display: flex;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    font-size: .20rem;
    span{
        width: 70%;
        border-radius: 0.05rem;
        line-height: .3rem;
        i{
            color: #f60;
            font-weight: bold;
            font-size: .16rem;
        }
    }
    button{
        font-size: .16rem;
        width: 30%;
        background:#f60;
        outline: none;
        border: none;
        border-radius: 0.05rem;
    }
}
</style>




