//引入路由
import Vue from 'vue'
import VueRouter from 'vue-router'
import Allgoods from '../components/Allgoods.vue'
import Index from '../components/Index.vue'
import Newpro from '../components/prolist/Newpro.vue'
import Numpro from '../components/prolist/Numpro.vue'
import Pracepro from '../components/prolist/Pracepro.vue'
import Shoppingcar from '../components/Shoppingcar.vue'
import User from '../components/User.vue'
import Login from '../components/login/Login.vue'
import Resgiter from '../components/resgiter/Resgiter.vue'
//使用路由
Vue.use(VueRouter)
//创建路由表
const router = new VueRouter({
    routes:[
        {
            path:'/',
            component:Index
        },
        {
            path:'/index',
            component:Index
        },
        {
            path:'/allgoods',
            component:Allgoods,
            children:[
                {
                    path:'/',
                    component:Newpro
                },
                {
                    path:'newpro',
                    component:Newpro
                },
                {
                    path:'numpro',
                    component:Numpro
                },
                {
                    path:'Pracepro',
                    component:Pracepro
                },
            ]
        },
        {
            path:'/shoppingcar',
            component:Shoppingcar
        },
        {
            path:'/user',
            component:User
        },
        {
            path:'/login',
            component:Login
        },
        {
            path:'/resgiter',
            component:Resgiter
        }
    ]
})
export default router;//导入路由


