<template>
    <div class="goods-box">
        <header class="good-header">
            <router-link to=''>
                <span class="fa fa-angle-left"></span>
            </router-link>
            <input type="text">
        </header>
        <ul class="pro-select">
            <li>
                <router-link to="/allgoods/newpro">新品</router-link>
            </li>
            <li>
                <router-link to="/allgoods/numpro">销量</router-link>
            </li>
            <li>
                <router-link to="/allgoods/pracepro">价格</router-link>
            </li>
        </ul>
        <router-view></router-view>
        <Last></Last>
    </div>
</template>

<script>
import Hotpro from './home/Hotpro';
import Last from './last/Last'
export default {
  name: 'Goods',
  components: {
      Hotpro,Last
  }
}

</script>

<style lang = 'scss'>
    .good-header{
        width: 100%;
        padding: 0 0.4rem;
        height: 0.4rem;
        position: relative;
        background: #f1f1f1;
        a{
            position: absolute;
            left: 0;
            top: 0;
            width: 0.4rem;
            height: 0.4rem;
            text-align: center;
            line-height: 0.4rem;
            float: left;
            font-size: 0.24rem;
            
        }
        input{
            margin: 0.05rem 0;
            width: 100%;
            height: 0.3rem;
            line-height: 0.3rem;
            background: #ddd;
            border: #ccc 0.01rem solid;
        }
    }
    .pro-select{
        width: 100%;
        height: 0.3rem;
        margin: 0.1rem 0px;
        border: #ddd 0.01rem solid;
        border-radius: 0.03rem;
        overflow: hidden;
        li{
            float: left;
            width: 33.33%;
            border-right: #ddd 0.01rem solid;
            a{
                background: #f60;
                color: #fff;    
                display: block;
                width: 100%;
                text-align: center;
                height: 0.3rem;
                line-height: 0.3rem;
            }
        }
    }
</style>




