import { SYNC_DATA } from "./const";

// action中处理的是异步请求的方法    以后的数据的请求/处理等方法全部写在这里
export default {
    initCars({commit}){
        //当数据更新之后初始化，直接出现cars
        let cars = getCars();
        commit(SYNC_DATA,cars)
    },
    addCar( {commit} , good ){
        // console.log('hahaha')
        //以下为后端操作数据
        
        setTimeout( () => {
            let cars = getCars();//获取本地数据
            let isHas = cars.some( item =>{
                if(item.id === good.id){
                    item.numb++;
                    return true
                }
            })//some 返回 true /false
            if(!isHas){
                good.numb = 1;
                cars.push( good );
            }
            localStorage.cars = JSON.stringify(cars)
            commit(SYNC_DATA,cars)
        },200)
    },
    reduceCar( {commit},good ){
        setTimeout(() => {
            let cars = getCars();//获取本地数据
            cars = cars.filter(item =>{
              if(item.id === good.id){
                  item.numb--
                  if(item.numb <= 0){
                      return false
                  }
                  return true
              }
              return true
            })
            
            localStorage.cars = JSON.stringify(cars)
            commit(SYNC_DATA,cars)
        },200)
    }
}
 function getCars(){
     return JSON.parse( localStorage.cars?localStorage.cars:'[]')
 }


