<template>
    <div class="main">
        <div class="username">
            <dl class="name">
                <dt>
                    <img src="" alt="">
                </dt>
                <dd>
                    <h3>测试文字</h3>
                </dd>
            </dl>
            <div class="shouye">
                <dl>
                    <dt>测试</dt>
                    <dd>100.00</dd>
                </dl>
                <dl>
                    <dt>测试</dt>
                    <dd>100</dd>
                </dl>
            </div>
        </div>
        <ul>
            <div>
                <dl>
                    <dt>
                        <router-link to=''><img src="" alt=""></router-link>
                    </dt>
                    <dd>
                        <router-link to=''></router-link>
                    </dd>
                </dl>
            </div>
            <li>
                <span></span>
                <router-link to=''>我的订单</router-link>
            </li>
        </ul>
        <div class="lrSub">
            <router-link to=''>退出登录</router-link>
        </div>
        <Last></Last>
    </div>
</template>

<script>
import Last from './last/Last'
export default {
    name:'User',
    components: {
        Last
    },
  methods: {
    aa(){
      //这里可以通过this.$store.commit()这样一个方法来触发mutations中方法
      setTimeout( () => {
        this.$store.commit('aa')
      },2000)
    }
  }
}
</script>

<style lang="scss" scoped>
    .username{
        color: #333;
        position: relative;
        height: 1.6rem;
        background: url(../../static/images/user01.png) left center repeat-x;
        .name{
            width: 2.8rem;
            padding: 0.23rem 0;
            margin: 0 auto;
            height: 1.02rem;
            dt{
                float: left;
                width: 0.5rem;
                height: 0.5rem;
                border-radius: 0.03rem;
                overflow: hidden;
                border: #fff 0.01rem solid;
                img{
                    width: 0.5rem;
                    height: 0.5rem;
                }
            }
            dd{
                float: right;
                width: 2rem;
                font-size: 1.4em;
                h3{
                    height: 0.3rem;
                    line-height: 0.3rem;
                    color: #fff;
                    font-size: 1.2em;
                }
            }
        }
        .shouye{
            padding: 0.07rem 0;
            height: 0.58rem;
            background: url(../../static/images/tou.png);
            dl{
                width: 50%;
                float: left;
                text-align: center;
                color: #fff;
                font-size: 1.2em;
                border-right: #ccc 0.01rem solid;
            }
        }
    }
    ul 
    {
        div{
            padding: 0.13rem 0;
            margin: 0px auto 0.1rem auto;
            dl{
                float: left;
                width: 25%;
                text-align: center;
                dt{
                    padding: 0.04rem 0;
                    a{
                        color: #333;
                    }
                }
                dd{
                    font-size: 1em;
                    padding: 0.02rem 0;
                    a{
                        color: #7b7b7b;
                    }
                }
            }
        }
        li{
            position: relative;
            height: 0.4rem;
            width: 100%;
            padding-left: 0.35rem;
            font-size: 0.13rem;
            background-color: #fff;
            border-bottom: #ddd 0.01rem solid;
            span{
                position: absolute;
                width: 0.2rem;
                left: 0.05rem;
                top: 0.12rem;
                color: #666;
                font-size: 0.18rem;
            }
            a{
                color: #333;
                display: block;
                line-height: 0.4rem;
                width: 100%;
                padding-right: 0.1rem;
                background: url(../../static/images/jian-new.png) right center no-repeat;
            }
        }
    }
    .lrSub {
        padding: 0.2rem 0.1rem;
        a{
            display: block;
            border: 0;
            padding: 0;
            margin: 0;
            font-size: 0.16rem;
            width: 100%;
            height: 0.4rem;
            line-height: 0.4rem;
            text-align: center;
            background: #f60;
            color: #fff;
            border-radius: 0.04rem;
        }
    }
</style>




