<template>
    <div class="home">
        <div class="search">
            <input type="text">
            <button>搜索</button>
        </div>
        <div class="login">
            <ul>
                <li><router-link to='/resgiter'>登录</router-link></li>
                <li><router-link to='/login'>注册</router-link></li>
            </ul>
        </div>
        <banner></banner>
        
    </div>
</template>

<script>
import Banner from './Banner.vue'
import Login from '../login/Login'
import Resgiter from '../resgiter/Resgiter'
export default {
    name:'home',
    components: {
        Banner,Login,Resgiter
    }
}
</script>

<style lang="scss" scoped>
        .home{
            margin: 0.5rem 0 0 0;
            width: 100%;
        }
        .search{
            padding: 0.1rem;
            overflow: hidden;
        }
        .search input{
            width: 77%;
            height: 0.3rem;
            line-height: 0.3rem;
            border: #ddd 0.01rem solid;
            border-radius: 0.03rem;
        }
        .search button{
            width: 18%;
            height: 0.3rem;
            line-height: 0.3rem;
            background: #f60;
            color: #fff;
            border-radius: 0.03rem;
            outline: none;
            border: none;
        }
        .login{
            padding: 0 0.05rem 0.1rem 0.05rem;
        }
        .login ul{
            display: flex;
            justify-content: space-around;
        }
        .login ul li{
            width: 45%;
            padding: 0 0.05rem;
        }
        .login ul li a{
            display: block;
            width: 100%;
            height: 0.35rem;
            line-height: 0.35rem;
            text-align: center;
            background: #f60;
            color: #fff;
        }
</style>


