<template>
    <div class="maincont">
        <div class="header-top">
            <router-link to="/index" class="back-off f1">
                <span class="fa fa-chevron-left"></span>
            </router-link>
            <div class="head-mid">
                <h1>会员注册</h1>
            </div>
            
        </div>
        <div class="head-top">
            <img src="../../../static/images/head.jpg" alt="">
        </div>
        <div class="res">
            <input type="text" placeholder="请输入用户名" v-model="isMsg.username">
            <input type="password" placeholder="请输入密码" v-model="isMsg.password">
            <button
                @click="addMsg(isMsg)"
            >立即注册</button>
        </div>
        
    </div>
</template>
<script>
import {mapActions} from 'vuex'
export default {
    data () {
        return {
            isMsg:{username:'',password:'',$router:this.$router}
        }
    },
    methods:{
        ...mapActions(['addMsg'])
    }
}
</script>

<style lang='scss'>
.maincont {
    width: 100%;
    margin: 0 auto;

}
    .header-top {
    padding: 0 0.4rem;
    height: 0.4rem;
    position: relative;
    background: #f1f1f1;

}

.back-off {

    position: absolute;
    left: 0;
    top: 0;
    width: 0.4rem;
    height: 0.4rem;
    text-align: center;
    line-height: 0.4rem;
    font-size: 1.4em;

}
.fl {

    float: left;

}
.head-mid h1 {

    font-size: 0.2rem;
    text-align: center;
    height: 0.4rem;
    line-height: 0.4rem;

}
.head-top {
    
    width:100%;
    position: relative;

}
.head-top img{
    width: 100%;
    height: auto;
    }
.res{
    padding: 0 0.08rem;
    background: #fff;
    input{
        margin: .3rem 0;
        width: 100%;
        height: 0.3rem;
        font: .2rem/.3rem "";
        outline: none;
        border:none;
        border-bottom: #ddd 0.01rem solid;
    }
    button{
        padding: 0.2rem 0.1rem;
        display: block;
        border: 0;
        padding: 0;
        margin: 0;
        font-size: 0.16rem;
        width: 100%;
        height: 0.4rem;
        line-height: 0.4rem;
        text-align: center;
        background: #f60;
        color: #fff;
        border-radius: 0.04rem;
    }
}
</style>



