<template>
    <div class="hotpro">
        <ul>
            <li 
                v-for="v in hotlist" :key="v.id"
            >
                <img :src="v.url" alt="">
                <h3>{{v.name}}</h3>
                <p>
                    <strong>￥{{v.prace}}</strong>
                    <span>￥{{v.oldPrace}}</span>
                </p>
                <div>
                    <span>{{v.discount}}折</span>
                    <i>已售：{{v.num}}</i>
                     <b
                        @click="addCar(v)"
                     >加入购物车</b>
                </div>
               
            </li>
        </ul>
    </div>
</template>
<script>
import Vue from 'vue';
import axios from 'axios';
import {mapActions} from 'vuex'
export default {
    name:'hotlist',
    data () {
        return {
            hotlist:[]
        }
    },
    methods: {
        ...mapActions(['addCar']),
        get(){
            axios.get('./static/hotlist.json')
            .then(res=>{
                this.hotlist = res.data;
            }).catch(err=>{
                console.log(err)
            })
        }
    },
    created () {
        this.get();
    }
}
</script>
<style lang="scss" scoped>
    ul{
        li{
            padding: 0.1rem;
            border-bottom: #ddd 0.01rem solid;
            img{
                width: 0.8rem;
                height: 0.8rem;
                padding-right: 0.1rem;
                float: left;
            }
            h3{
                line-height: 0.3rem;
                font-weight: bold;
            }
            p{
                color: #f60;
                line-height: 0.3rem;
                height: 0.3rem;
                span{
                    color: #999;
                    text-decoration: line-through;
                    margin-left: 10px;
                }
            }
            div{
                height: 0.2rem;
                line-height: 0.2rem;
                span{
                    display: inline-block;
                    height: 0.2rem;
                    line-height: 0.2rem;
                    width: 0.5rem;
                    text-align: center;
                    font-size: 0.12rem;
                    color: #fff;
                    background: #f60;
                }
                i{
                    font-style: normal;
                    font-size: 0.12rem;
                    color: #999;
                    margin-left: 0.1rem;
                }
                b{
                 cursor: pointer;
                  width: .15rem;
                  height: .18rem;
                  background: #f26;  
                  color:#fff;
                  border-radius: .02rem;
                }
            }
        }
    }
</style>



